import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Switch from "@material-ui/core/Switch";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons
import ImportContacts from "@material-ui/icons/ImportContacts";
import RadioIcon from '@material-ui/icons/Radio';
// core components
import Header from "components/Header/Header.js";
import Footer from "components/Footer/Footer.js";
import Button from "components/CustomButtons/Button.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import NavPills from "components/NavPills/NavPills.js";
import Parallax from "components/Parallax/Parallax.js";
import CustomInput from "components/CustomInput/CustomInput.js";

import profile from "assets/img/faces/christian.jpg";

import habit from "assets/img/examples/the-power-of-habit-cover.png";
import happiness from "assets/img/examples/the-happiness-advantage-cover.jpg";
import rules from "assets/img/examples/12-rules-of-life-cover.jpg";
import lessons from "assets/img/examples/lessons-of-a-lakota-cover.jpg";
import mentor from "assets/img/examples/the-mindset-mentor.jpeg";
import ted from "assets/img/examples/ted-talks-daily.png";
import minute from "assets/img/examples/one-minute.jpg";
import greatness from "assets/img/examples/the-school-of-greatness.jpg";

import styles from "assets/jss/material-kit-react/views/profilePage.js";

const useStyles = makeStyles(styles);

export default function ProfilePage(props) {
  const classes = useStyles();
  const [checked, setChecked] = React.useState([24, 22]);
  const [checkedA, setCheckedA] = React.useState(false);
  const { ...rest } = props;
  const imageClasses = classNames(
    classes.imgRaised,
    classes.imgRoundedCircle,
    classes.imgFluid
  );
  const navImageClasses = classNames(classes.imgRounded, classes.imgGallery);
  return (
    <div>
      <Header
        color="transparent"
        brand="Mind Over Matter"
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 200,
          color: "white"
        }}
        {...rest}
      />
      <Parallax small filter image={require("assets/img/bg5.jpg")}>
        <div className={classes.parallaxContainer}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={6}>
              <h1 className={classes.parallaxTitle}>Mind</h1>
            </GridItem>
          </GridContainer>
        </div>
      </Parallax>
      <div className={classNames(classes.main, classes.mainRaised)}>
        <div>
          <div className={classes.container}>
            <GridContainer justify="center">
              <GridItem xs={12} sm={12} md={8}>
                <div className={classes.profile}>
                  <h3 className={classes.title}>"Imagination is more important than knowledge. Knowledge is limited. Imagination encircles the world."</h3>
                </div>
              </GridItem>
            </GridContainer>
            <GridContainer justify="center">
              <div className={classes.description}>
                <p>
                  I still remember when I started to get my head straight. Let me take you back to when I was in highschool. 
                  I was lazy so my marks were always low, I was around people who liked picking fights, cheated on tests and lied. 
                  It didn't occured to me yet, but thinking back I am still grateful that I met the people I crossed paths with. 
                  The reason was because if it wasn't for them I wouldn't be who I am today. This was just grade 9, so I was at a young age. 
                  I've gone through a few things that shaped my mind to what it is now. My mind first started to truly evolve when I 
                  joined the dragon boat team in grade 9. I was very skinny, only weighing about 110 pounds which at the time didn't
                  affected me much until people kept pointing out how skinny I was. Anyways, during the dragon boat season we worked hard,
                  we trained until we pretty much dropped. The thing is, the team was so large, we were all in it together which is why we all
                  just kept going until some gave up and left the team. Those who stayed were pretty much those who had the mindset to
                  just keep showing up. That's when I realized that if you just show up and put in the work, you will see results. I probably
                  gained about 5 pounds of muscle from joining the team.  Just seeing those results motivated me to keep working out, so the
                  following summer I decided to workout in my backyard with only a steel rod, two buckets and a bucket load of stones. 
                  I probably gained another 5 pounds that summer, enough for my class mates to notice a transformation. The beauty of it all,
                  of choosing to continue with the progess was the fact that it spilled over to the rest of my bad habits. I decided to started
                  listening in class which brought my grades from C's and D's to B's and A's. I started to improve my grades to a point where my
                  smart friends couldn't believe it, they genuienely believed I was cheating. I also decided to take physics class, my friends
                  kept mentioning how hard it was, that I should drop the course but I decided to ignore it and still went for it. My previouse science
                  teacher who I had, was my physics teacher then, and she asked me if I was sure that I wanted to take physics, I told her
                  "why not?". It's funny because even my physics teacher didn't believe I could do it, but to her surprise I did way better 
                  than what she expected (she told me that herself). Safe to say, I had fun proving people what I was capable of. The reason I
                  mentioned this to you is not for recognition but to demonstrate what one can do if they decided to put in the work, think of
                  what you want and go after it, ignore those who speak out of fear, and use the comments from naysayers as fuel. See how the
                  body, and spirit played a role in this the minds development? How working out contribute to the development of the mind and how
                  believing in yourself contributed to your process? 
                </p>
              </div>
            </GridContainer>
            <GridContainer justify="center">
              <GridItem xs={12} sm={12} md={8} className={classes.navWrapper}>
                <NavPills
                  alignCenter
                  color="primary"
                  tabs={[
                    {
                      tabButton: "Books",
                      tabIcon: ImportContacts,
                      tabContent: (
                        <GridContainer justify="center">
                          <GridItem xs={12} sm={12} md={4}>
                            <img
                              alt="..."
                              src={habit}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={rules}
                              className={navImageClasses}
                            />
                          </GridItem>
                          <GridItem xs={12} sm={12} md={4}>
                            <img
                              alt="..."
                              src={lessons}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={happiness}
                              className={navImageClasses}
                            />
                          </GridItem>
                        </GridContainer>
                      )
                    },
                    {
                      tabButton: "Pod Cast",
                      tabIcon: RadioIcon,
                      tabContent: (
                        <GridContainer justify="center">
                          <GridItem xs={12} sm={12} md={4}>
                            <img
                              alt="..."
                              src={mentor}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={ted}
                              className={navImageClasses}
                            />
                          </GridItem>
                          <GridItem xs={12} sm={12} md={4}>
                            <img
                              alt="..."
                              src={minute}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={greatness}
                              className={navImageClasses}
                            />
                          </GridItem>
                        </GridContainer>
                      )
                    }
                  ]}
                />
              </GridItem>
            </GridContainer>
            <GridContainer justify="center">
              <GridItem cs={12} sm={12} md={8}>
                <h2 className={classes.shareTitle}>Share with us</h2>
                <h4 className={classes.shareDescription}>
                  The mind is always being challenged, internally and externally. Share with us an experiences or if you have any
                  thought you'd like to share then feel free. If you are not comfortable with sharing, you can always come back.
                </h4>
                <div>
                  <FormControlLabel
                    control={
                      <Switch
                        checked={checkedA}
                        onChange={event => setCheckedA(event.target.checked)}
                        value="checkedA"
                        classes={{
                          switchBase: classes.switchBase,
                          checked: classes.switchChecked,
                          thumb: classes.switchIcon,
                          track: classes.switchBar
                        }}
                      />
                    }
                    classes={{
                      label: classes.label
                    }}
                    label="Remain Anonymous"
                  />
                </div>
                <form>
                  <GridContainer>
                    <GridItem xs={12} sm={12} md={6}>
                      <CustomInput
                        labelText="Your Name"
                        id="name"
                        formControlProps={{
                          fullWidth: true
                        }}
                      />
                    </GridItem>
                    <GridItem xs={12} sm={12} md={6}>
                      <CustomInput
                        labelText="Your Email"
                        id="email"
                        formControlProps={{
                          fullWidth: true
                        }}
                      />
                    </GridItem>
                    <CustomInput
                      labelText="Your Story"
                      id="message"
                      formControlProps={{
                        fullWidth: true,
                        className: classes.textArea
                      }}
                      inputProps={{
                        multiline: true,
                        rows: 5
                      }}
                    />
                    <GridItem xs={12} sm={12} md={4}>
                      <Button color="primary">Share Story</Button>
                    </GridItem>
                  </GridContainer>
                </form>
              </GridItem>
            </GridContainer>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}