import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Switch from "@material-ui/core/Switch";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons
import Camera from "@material-ui/icons/Camera";
import Palette from "@material-ui/icons/Palette";
import Favorite from "@material-ui/icons/Favorite";
import ImportContacts from "@material-ui/icons/ImportContacts";
import Theaters from "@material-ui/icons/Theaters";
import MusicNote from "@material-ui/icons/MusicNote";
// core components
import Header from "components/Header/Header.js";
import Footer from "components/Footer/Footer.js";
import Button from "components/CustomButtons/Button.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import NavPills from "components/NavPills/NavPills.js";
import Parallax from "components/Parallax/Parallax.js";
import CustomInput from "components/CustomInput/CustomInput.js";

import profile from "assets/img/faces/christian.jpg";

import unbroken from "assets/img/examples/unbroken-cover.jpg";
import lion from "assets/img/examples/lion-cover.jpg";
import force from "assets/img/examples/a-force-for-good-cover.jpg";
import patch from "assets/img/examples/patch-adams.jpg";
import forrest from "assets/img/examples/forrest-gump.png";
import bucket from "assets/img/examples/the-bucket-list.jpg";
import pursuit from "assets/img/examples/the-pursuit-of-happyness.png";
import monk from "assets/img/examples/think-like-a-monk-cover.jpg";

import styles from "assets/jss/material-kit-react/views/profilePage.js";

const useStyles = makeStyles(styles);

export default function ProfilePage(props) {
  const classes = useStyles();
  const [checked, setChecked] = React.useState([24, 22]);
  const [checkedA, setCheckedA] = React.useState(false);
  const { ...rest } = props;
  const imageClasses = classNames(
    classes.imgRaised,
    classes.imgRoundedCircle,
    classes.imgFluid
  );
  const navImageClasses = classNames(classes.imgRounded, classes.imgGallery);
  return (
    <div>
      <Header
        color="transparent"
        brand="Mind Over Matter"
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 200,
          color: "white"
        }}
        {...rest}
      />
      <Parallax small filter image={require("assets/img/bg6.jpg")}>
        <div className={classes.parallaxContainer}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={6}>
              <h1 className={classes.parallaxTitle}>Spirit</h1>
            </GridItem>
          </GridContainer>
        </div>
      </Parallax>
      <div className={classNames(classes.main, classes.mainRaised)}>
        <div>
          <div className={classes.container}>
            <GridContainer justify="center">
              <GridItem xs={12} sm={12} md={6}>
                <div className={classes.profile}>
                  <h3 className={classes.title}>"Great Spirits have always encountered violent opposition from mediocre minds."</h3>
                </div>
              </GridItem>
            </GridContainer>
            <div className={classes.description}>
              <p>
                As a young age I grew up in a Catholic community, went to church, did my confessions, said my prayers and so on. The thing is religion
                was not fun, but I'd always pray to God whenever I needed or wanted something. Anyways this isn't about religion but about the human spirit.
                We as humans can endure so much just as long as our spirit and mind can hold strong. I remember when my mom got cancer, it was hard on my dad,
                my siblings, and myself. Fortunetly she survived it, and I thank God for that. This however isn't a story about my journey but my mother.
                I was still in highschool when we got the news, my mom was always cheerful, energetic and a hard worker. When she discovered that she had
                colon cancer, everything changed for her. Her life went from enoying time with friends and family to sitting at a hospital bed treated with
                chemotherapy. She has gone through a bunch of surgeries and experienced an incredible amount of pain and fear. During her stay at the hospital 
                she refused to take gravol every day because she didn't want her body to get dependent on it, rather she wanted to be capable eanough to withstand 
                her pain. It was a few months after that she was finally released from the hospital, however her fight wasn't over. She still had to wear 
                this tube that comes out her colon because it hasn't reach its normal function with all the treatments.  Not only that she had to spend months 
                at home with chemotherapy. This joruney can bring anyone down for a long time, and some may not even come back from it. Despite the hardship she 
                experienced, she remained the same, undefeated. It was only a few months after she was released that it occured to me that my mom's spirit is
                strong. She has gone through multiple losses, yet she remained undefeated. She experienced criticism, yet she remained unshakable. It is in her
                nature to be strong, which is why I consider her a hero. She never complained, never talked ill about anyone, approached everyone with a smile
                and always treated people with kindess. She never wavered from anything she believed in. The reason I share this story is because of the strenth
                the spirit is capable of. You may think, this could be about the mind staying positive and strong, but how can it just be about that? You can 
                tell yourself positive thoughts and force yourself to be happy but what about the belief? My mom remained strong because she valued her family,
                her friends, and remained her kindself because she believed in treating others the way she'd like to be treated. Being around her has lifted 
                my spirit to become a better person, a person just like her.
              </p>
            </div>
            <GridContainer justify="center">
              <GridItem xs={12} sm={12} md={8} className={classes.navWrapper}>
                <NavPills
                  alignCenter
                  color="primary"
                  tabs={[
                    {
                      tabButton: "Books",
                      tabIcon: ImportContacts,
                      tabContent: (
                        <GridContainer justify="center">
                          <GridItem xs={12} sm={12} md={4}>
                            <img
                              alt="..."
                              src={unbroken}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={lion}
                              className={navImageClasses}
                            />
                          </GridItem>
                          <GridItem xs={12} sm={12} md={4}>
                            <img
                              alt="..."
                              src={force}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={monk}
                              className={navImageClasses}
                            />
                          </GridItem>
                        </GridContainer>
                      )
                    },
                    {
                      tabButton: "Movies",
                      tabIcon: Theaters,
                      tabContent: (
                        <GridContainer justify="center">
                          <GridItem xs={12} sm={12} md={4}>
                            <img
                              alt="..."
                              src={patch}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={forrest}
                              className={navImageClasses}
                            />
                          </GridItem>
                          <GridItem xs={12} sm={12} md={4}>
                            <img
                              alt="..."
                              src={bucket}
                              className={navImageClasses}
                            />
                            <img
                              alt="..."
                              src={pursuit}
                              className={navImageClasses}
                            />
                          </GridItem>
                        </GridContainer>
                      )
                    }
                  ]}
                />
              </GridItem>
            </GridContainer>
            <GridContainer justify="center">
              <GridItem cs={12} sm={12} md={8}>
                <h2 className={classes.shareTitle}>Share with us</h2>
                <h4 className={classes.shareDescription}>
                  A spirit isn't something that is visible, however you can really feel another persons spirits when you look them in
                  the eye. The eyes are truly the window to a human soul, you can really tell when a persons spirit has been beat or
                  has been lifted. Share your story on any encounter or anything that tested you and maybe someone out there will be
                  able to relate.
                </h4>
                <div>
                  <FormControlLabel
                    control={
                      <Switch
                        checked={checkedA}
                        onChange={event => setCheckedA(event.target.checked)}
                        value="checkedA"
                        classes={{
                          switchBase: classes.switchBase,
                          checked: classes.switchChecked,
                          thumb: classes.switchIcon,
                          track: classes.switchBar
                        }}
                      />
                    }
                    classes={{
                      label: classes.label
                    }}
                    label="Remain Anonymous"
                  />
                </div>
                <form>
                  <GridContainer>
                    <GridItem xs={12} sm={12} md={6}>
                      <CustomInput
                        labelText="Your Name"
                        id="name"
                        formControlProps={{
                          fullWidth: true
                        }}
                      />
                    </GridItem>
                    <GridItem xs={12} sm={12} md={6}>
                      <CustomInput
                        labelText="Your Email"
                        id="email"
                        formControlProps={{
                          fullWidth: true
                        }}
                      />
                    </GridItem>
                    <CustomInput
                      labelText="Your Story"
                      id="message"
                      formControlProps={{
                        fullWidth: true,
                        className: classes.textArea
                      }}
                      inputProps={{
                        multiline: true,
                        rows: 5
                      }}
                    />
                    <GridItem xs={12} sm={12} md={4}>
                      <Button color="primary">Share Story</Button>
                    </GridItem>
                  </GridContainer>
                </form>
              </GridItem>
            </GridContainer>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}
