import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import Header from "components/Header/Header.js";
import Footer from "components/Footer/Footer.js";
import Button from "components/CustomButtons/Button.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import NavPills from "components/NavPills/NavPills.js";
import Parallax from "components/Parallax/Parallax.js";

import styles from "assets/jss/material-kit-react/views/forumPage.js";

const useStyles = makeStyles(styles);

export default function ProfilePage(props) {
  const classes = useStyles();
  const { ...rest } = props;
  return (
    <div>
      <Header
        color="transparent"
        brand="Mind Over Matter"
        rightLinks={<HeaderLinks />}
        fixed
        changeColorOnScroll={{
          height: 200,
          color: "white"
        }}
        {...rest}
      />
      <Parallax small filter image={require("assets/img/bg6.jpg")}>
        <div className={classes.parallaxContainer}>
          <GridContainer>
            <GridItem xs={12} sm={12} md={6}>
              <h1 className={classes.parallaxTitle}>Forum</h1>
            </GridItem>
          </GridContainer>
        </div>
      </Parallax>
      <div className={classNames(classes.main, classes.mainRaised)}>
        <div>
          <div className={classes.container}>
            <GridContainer>
              <GridItem xs={12} sm={12} md={6}>
                <div>
                  <h3 className={classes.title}>Kevin Freire</h3>
                </div>
              </GridItem>
            </GridContainer>
            <div className={classes.description}>
              <p>
                As a full time employee and part time student my life has been extremly busy. It really tested me
                in many ways. The amount of stress I experience both at work and in school takes my time away from
                meditating and reading. I had to put those two aside, and with the pandemic it made it more difficult
                to exercise. Going into the third lockdown it gets frustrating, but when the gyms open it makes a 
                difference. I've developed a larger appreciation towards the gym because it became more than just
                lifting weights, its become a place to declutter my mind and release any internal stress. The pandemic
                pushed me to a different view point of the gym and I am grateful for that.
              </p>
            </div>
          </div>
        </div>
        <div>
          <div className={classes.container}>
            <GridContainer>
              <GridItem xs={12} sm={12} md={6}>
                <div>
                  <h3 className={classes.title}>Anonymous</h3>
                </div>
              </GridItem>
            </GridContainer>
            <div className={classes.description}>
              <p>
                I like to remain anonymous for many reason, however I am not here to write about that. I'd like to share
                an experience I had that tested both my mind, body and spirit. I was captain of the football team and 
                had a bright future ahead of me. One day on August 17th, 2018 I was playing against our rival team for the 
                championship. Unfortunetly I was tackled, broke my elbow and sprained my ankle. The doctor told me I was
                never going to be able to play footbal again. This broke me, I felt so hopeless and took out my frustration
                towards my family. Nevertheless, I was in a dark pit. I was off the team for over a year until I fully
                recovered, however the voice of my doctor still lingered in my mind "you will never play again". I still
                remebered going on a walk by myself in the woods to clear my head, when it suddenly hit me. It was like a
                strike of lightning, where I came to the realization that no one will determine what I can and can't do. It
                was a very uplifting feeling where I truly felt like I can do anything I put my mind to. So from there on I 
                promised myself that I would achieve anything I want, starting with bringing my team back on top. I trained
                rigorously to get back in shape while making sure I did not injure myself. I ate healthier and began reading
                more to keep my mind sharp (also a good way to calm my mind). After a few months of training I joined the
                team once again. Ofcourse I was rusty but I quickly picked up where I left. We lost a few games but we won
                more and more until eventually we got close to the top. I felt incredibly grateful to be back, and for 
                the mental ability to just push myself. I told myself, "no matter what, I will remain grateful for the 
                opportunity to get back up". Long story short, we came in second but I still felt my own personal victory of
                my comeback, and going against the odds. Life truly is inredibly odd and unpredictable but we can't let 
                external circumstances dictate our own future, I hope many of you find inspiration with my story and apply
                the same strngth towards anything.
              </p>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}
